.content {
    padding: 0 0 20px;
}

.content .content__head {
    margin-top: 0px !important;
    margin-bottom: 30px !important;
}

.content__head {
    background-color: #28282d;
    -webkit-box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.3);
    /* margin-top: 70px; */
    position: relative;
}

.content__head:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    display: block;
    background-image: -moz-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: -webkit-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: -ms-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    -webkit-box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
    box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
}

.content__head--profile:before {
    display: none;
}

.content__title {
    color: #fff;
    font-weight: 300;
    font-size: 28px;
    margin: 20px 0 5px;
}

.content__tabs {
    display: none;
}

.content__mobile-tabs {
    position: relative;
}

.content__mobile-tabs--profile {
    margin-bottom: -20px;
}

.content__mobile-tabs-btn {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    height: 50px;
    opacity: 0.75;
    -webkit-transition: 0.4s ease;
    -moz-transition: 0.4s ease;
    transition: 0.4s ease;
}

.content__mobile-tabs-btn input {
    display: block;
    position: relative;
    width: auto;
    background-color: transparent;
    border: none;
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    height: 50px;
    margin-right: 12px;
    letter-spacing: 0.2px;
    font-weight: 300;
}

.content__mobile-tabs-btn span {
    position: relative;
    width: 16px;
    height: 16px;
    display: block;
}

.content__mobile-tabs-btn span:before, .content__mobile-tabs-btn span:after {
    content: '';
    position: absolute;
    display: block;
    width: 16px;
    height: 2px;
    background-color: #fff;
    left: 0;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    top: 50%;
    -webkit-transition: 0.4s ease;
    -moz-transition: 0.4s ease;
    transition: 0.4s ease;
    margin-top: -5px;
}

.content__mobile-tabs-btn span:after {
    margin-top: 2px;
    width: 10px;
}

.content__mobile-tabs-btn[aria-expanded="true"] {
    opacity: 1;
}

.content__mobile-tabs-btn[aria-expanded="true"] span:before {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-top: -1px;
}

.content__mobile-tabs-btn[aria-expanded="true"] span:after {
    margin-top: -1px;
    width: 16px;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.content__mobile-tabs-btn:hover {
    opacity: 1;
}

.content__mobile-tabs-menu {
    top: 100%;
    left: 0;
    margin-top: -10px;
    min-width: 200px;
    padding: 15px 20px;
    -webkit-box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.3);
    background-color: #2b2b31;
    display: block;
    -webkit-transition: 0.4s ease;
    -moz-transition: 0.4s ease;
    transition: 0.4s ease;
    position: absolute;
    z-index: 100;
    pointer-events: none;
    opacity: 0;
}

.content__mobile-tabs-menu:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    display: block;
    background-image: -moz-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: -webkit-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: -ms-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    background-image: linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
    -webkit-box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
    box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
}

.content__mobile-tabs-menu a {
    display: block;
    line-height: 40px;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.75);
    position: relative;
    text-transform: uppercase;
    font-weight: 300;
}

.content__mobile-tabs-menu a:hover {
    color: #ff55a5;
}

.content__mobile-tabs-menu a.active {
    display: none;
}

.content__mobile-tabs-menu.show {
    pointer-events: auto;
    opacity: 1;
    margin-top: 0;
}

@media (min-width: 576px) {
    .content__title {
        font-size: 30px;
        margin: 25px 0 5px;
    }
    .content__tabs {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        overflow: hidden;
    }
    .content__tabs li {
        margin-right: 30px;
    }
    .content__tabs li:last-child {
        margin-right: 0;
    }
    .content__tabs a {
        display: block;
        line-height: 50px;
        color: #fff;
        position: relative;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 0.2px;
        color: rgba(255, 255, 255, 0.5);
    }
    .content__tabs a:hover {
        color: #fff;
    }
    .content__tabs a:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 2px;
        display: block;
        background-image: -moz-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
        background-image: -webkit-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
        background-image: -ms-linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
        background-image: linear-gradient(90deg, #ff55a5 0%, #ff5860 100%);
        -webkit-box-shadow: none;
        box-shadow: none;
        -webkit-transition: 0.4s ease;
        -moz-transition: 0.4s ease;
        transition: 0.4s ease;
        -webkit-transform: translateY(2px);
        -moz-transform: translateY(2px);
        transform: translateY(2px);
    }
    .content__tabs a.active {
        color: #fff;
    }
    .content__tabs a.active:before {
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        transform: translateY(0);
        -webkit-box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
        box-shadow: 0 0 20px 0 rgba(255, 88, 96, 0.5);
    }
    .content__tabs--profile {
        display: none;
    }
    .content__mobile-tabs {
        display: none;
    }
    .content__mobile-tabs--profile {
        display: block;
    }
}

@media (min-width: 768px) {
    .content {
        padding: 0 0 30px;
    }
    .content .content__head {
        margin-bottom: 50px !important;
    }
    .content__title {
        font-size: 32px;
        margin: 25px 0 10px;
    }
    /* .content__head {
        margin-top: 90px;
    } */
    .content__tabs--profile {
        display: flex;
    }
    .content__tabs--profile a {
        line-height: 90px;
    }
    .content__mobile-tabs--profile {
        display: none;
    }
}

@media (min-width: 1200px) {
    .content__title {
        font-size: 36px;
    }
    .content__tabs--profile li {
        margin-right: 45px;
    }
    .content__tabs--profile li:last-child {
        margin-right: 0;
    }
}